body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.App {
    display: flex;
    flex-wrap: wrap;
    gap: 10px 20px; /* row-gap column gap */
    justify-content: center;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hall__stage {
    background-color: darkgray;
    display: block;
    font-weight: bold;
    text-transform: uppercase;
    border-radius: 5px;
}

.seats {
    background-color: green;
    border-radius: 5px;
}

.koszyk {
    display: inline-flex;
}

.label-x, .label-y, .empty-cell {
    font-weight: bold;
    background-color: #d65a3c;
}

.seat-cell {
    min-height: 40px;
    min-width: 40px;
    height: 40px;
    width: 40px;
    border: 1px solid black;
    position: relative;
}

.seat-cell:hover {
    background-color: #90a942;
    font-weight: bold;
}

.seat-cell:hover .seat-label {
    display: block;
}

.seat-label {
    position: absolute;
    font-weight: normal;
    top: -45px;
    left: -10px;
    width: 120px;
    display: none;
    background-color: azure;
    border-radius: 5px;
    padding: 4px;
}

td[data-category='A'] {
    background-color: green;
    font-weight: 500;
}

td[data-category='B'] {
    background-color: #009000;
}

td[data-category='C'] {
    background-color: #00a500;
}

td[data-category='D'] {
    background-color: #00be00;
}

td[data-selected='yes'], .seat-cell[data-selected='yes']:hover {
    background-color: #75c0ff;
}

.hall {
    position: relative;
    border-radius: 5px;
}

.ticket-summary {
    min-width: 80px;
    text-align: left;
    position: relative;
}

.ticket-summary-price {
    position: absolute;
    right: 0;
}

.content {
    display: inline-block;
    min-width: 90px;
}

.summary {
    flex-basis: 200px;
}

.summary-body {
    background-color: #e0e0e0;
    padding: 5px;
    border:1px solid black;
    border-radius:5px;
}

.error-message {
    position: fixed;
    top: 30px;
    background-color: #ff7e00;
    width: 400px;
    height: 80px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px black solid;

}

.error-message > div {
    font-weight: bold;
}

.steps {
    flex: 0 0 100%;
    background-color: azure;
}

.edit-panel {
    flex: 0 0 100%;
    background-color: lightgrey;
}

.edit-panel table td {
    padding: 8px;
}

.edit-panel table tr {
    border-bottom: solid 1px black;
}

.edit-panel__message {
    position: fixed;
    top: 30px;
    background-color: #e9e9e9;
    width: 400px;
    height: 80px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px black solid;
}